.about {
  &-block {
    padding: 2rem;
    background-color: $color-light;
    border-radius: .5rem;
  }

  &-btn {
    display: block;
    min-width: 40%;
    margin: 0 auto;
    outline: none;
    padding: 1rem;
    font-size: 1rem;
    font-weight: bold;
    margin-top: .5rem;
    border: none;
    background-color: $color-main;
    color: $color-white;
    border-radius: .5rem;
  }
}
